import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import routers from '@src/routers'
import { Layout } from 'antd'
import Header from '@components/Header'
import PrivateRoute from '@components/PrivateRoute'

function App() {
  return (
    <Router>
      <Layout> 
        <Header />
        <Switch>
          {
            routers.map(r => r.private ? <PrivateRoute route={r} /> : <Route path={r.path} component={r.component} exact={r.exact} />)
          }
        </Switch>
      </Layout>
    </Router>
  );
}

export default App;
